<template lang="pug">
v-container#cart
  v-data-table(
    :headers="headers"
    :items="products"
    :items-per-page="5"
    class="elevation-1"
    :item-class='rowClass'
  )
    template(#item.product.image="{ item }")
      v-img(:src='item.product.image[0]' height='60' width='100' contain)
    template(#item.product.price="{ item }")
      v-subheader $ {{ item.product.price }}
    template(#item.quantity="{ item }")
      v-form.spin-button.mt-7.mr-4.d-flex
        v-text-field.mr-7(
          v-if='!item.product.color&&!item.product.size'
          readonly
          dense
          width='20'
          type='number'
          v-model.number='item.quantity'
          min='10'
          :rules='qtyrules'
          hide-spin-buttons
          outlined
          append-icon='mdi-plus'
          prepend-inner-icon='mdi-minus'
          @click:append='item.quantity++;updateCart(item._id, item.quantity)'
          @click:prepend-inner='item.quantity > 1 ? item.quantity-- : null;updateCart(item._id, item.quantity)'
          @input='updateCart(item._id, item.quantity)'
          style='width: 30px'
        )
        v-text-field.mr-4(
          v-else
          disabled
          readonly
          dense
          width='20'
          type='number'
          v-model.number='item.quantity'
          min='10'
          :rules='qtyrules'
          hide-spin-buttons
          outlined
          append-icon='mdi-plus'
          prepend-inner-icon='mdi-minus'
          @click:append='item.quantity++;updateCart(item._id, item.quantity)'
          @click:prepend-inner='item.quantity > 1 ? item.quantity-- : null;updateCart(item._id, item.quantity)'
          @input='updateCart(item._id, item.quantity)'
          style='width: 20px'
        )
        router-link.text-decoration-none(v-if='item.product.color||item.product.size' :to='"/product/" + item.product._id')
          v-btn.mr-2.text-button(color='primary') ÂéªÂä†Ë≥º
        v-btn.text-button(color='error' @click='updateCart(item._id, 0)') Âà™Èô§
      h2.ml-15.white--text(v-if='!item.product.sell') ÂïÜÂìÅÂ∑≤‰∏ãÊû∂
    template(#item.custom="{ item }")
      ul
        li(v-for='cus in item.custom' v-if='cus') {{ cus }}
  h1.mt-4 Á∏ΩÈáëÈ°ç {{ total.toString() }}
  v-btn.mt-4(width='180' @click='checkout' color='error' :disabled='products.length === 0')
    p.sd-font.mb-1 ÁµêÂ∏≥
</template>

<script>

export default {
  name: 'Cart',
  components: {

  },
  data () {
    return {
      headers: [
        {
          text: 'ÂúñÁâá',
          align: 'start',
          sortable: false,
          value: 'product.image'
        },
        {
          text: 'ÂïÜÂìÅ',
          sortable: false,
          value: 'product.name'
        },
        { text: 'ÂÉπÊ†º', value: 'product.price' },
        { text: 'Êìç‰Ωú', value: 'quantity' },
        { text: 'ÂÇôË®ª', value: 'custom' }
      ],
      products: [],
      qtyrules: [
        v => v > 0 || 'Êï∏ÈáèÂøÖÈ†àÂ§ßÊñº 0',
        v => !!v || 'ÂøÖÂ°´È†ÖÁõÆ'
      ]
    }
  },
  methods: {
    rowClass (item) {
      // console.log(item)
      // const rowClass = 'rowClass'
      return !item.product.sell ? 'error' : ''
    },
    async checkout () {
      try {
        // post Á¨¨‰∫åÂÄãÊòØÈÄÅÂá∫ÂéªÁöÑÊù±Ë•øÔºåÊ≤íÊúâË¶Å post ÁöÑ‰πüÂøÖÈ†àÊîæÂÄãÁ©∫ÂÄã {}
        await this.api.post('/orders', {}, {
          headers: {
            authorization: 'Bearer ' + this.user.token
          }
        })
        this.$router.push('/orders')
        // ÁµêÂ∏≥ÂæåË≥ºÁâ©ËªäÂ∞èÊï∏Â≠óÊ≠∏Èõ∂
        this.$store.commit('user/updateCart', 0)
        this.$swal({
          icon: 'success',
          title: 'ÊàêÂäü',
          text: 'ÁµêÂ∏≥ÊàêÂäüüòÜ'
        })
      } catch (error) {
        this.$swal({
          icon: 'error',
          title: 'Â§±Êïó',
          text: 'ÁµêÂ∏≥Â§±Êïó'
        })
      }
    },
    // test () {
    //   console.log(this.products)
    // },
    async updateCart (id, quantity) {
      try {
        const index = this.products.map(item => item._id).indexOf(id)
        await this.api.patch('/users/me/cart',
          { product: this.products[index].product._id, quantity },
          {
            headers: {
              authorization: 'Bearer ' + this.user.token
            }
          }
        )
        // Âà™Èô§ÊåâÈàï
        if (quantity === 0) {
          this.products.splice(index, 1)
          // Ë≥ºÁâ©ËªäÁöÑÊï∏Â≠óÊâçÊúÉÂèäÊôÇÊõ¥Êñ∞
          this.$store.commit('user/updateCart', this.user.cart - 1)
        }
      } catch (error) {
        this.$swal({
          icon: 'error',
          title: 'Â§±Êïó',
          text: '‰øÆÊîπË≥ºÁâ©ËªäÂ§±Êïó'
        })
      }
    }
  },
  computed: {
    total () {
      return this.products.reduce((accumulator, currentValue) => {
        return accumulator + currentValue.quantity * currentValue.product.price
      }, 0)
    }
  },
  async created () {
    try {
      const { data } = await this.api.get('/users/me/cart', {
        headers: {
          authorization: 'Bearer ' + this.user.token
        }
      })
      this.products = data.result
    } catch (error) {
      this.$swal({
        icon: 'error',
        title: 'Â§±Êïó',
        text: 'ÂèñÂæóË≥ºÁâ©ËªäÂ§±Êïó'
      })
    }
  }
}
</script>
